<template>
  <div class="page bh-index-page">
    <div class="bh-search-mod">
      <div class="weui-search-bar">
        <div class="weui-search-bar__form">
          <div class="weui-search-bar__box">
            <icon class="weui-icon-search_in-box" type="search" size="14"></icon>
            <input type="text" class="weui-search-bar__input" placeholder="搜索" v-model="inputVal" :focus="inputShowed" @input="inputTyping" />
            <div class="weui-icon-clear" v-if="inputVal.length > 0" @click="clearInput">
              <icon type="clear" size="14"></icon>
            </div>
          </div>
          <label class="weui-search-bar__label" :hidden="inputShowed" @click="showInput">
            <icon class="weui-icon-search" type="search" size="14"></icon>
            <div class="weui-search-bar__text">搜索</div>
          </label>
        </div>
        <div class="weui-search-bar__cancel-btn" :hidden="!inputShowed" @click="hideInput">取消</div>
      </div>
      <div class="weui-cells searchbar-result" v-if="inputVal.length > 0">
        <navigator url="" class="weui-cell" hover-class="weui-cell_active">
          <div class="weui-cell__bd">
            <div>实时搜索文本1</div>
          </div>
        </navigator>
        <navigator url="" class="weui-cell" hover-class="weui-cell_active">
          <div class="weui-cell__bd">
            <div>实时搜索文本2</div>
          </div>
        </navigator>
        <navigator url="" class="weui-cell" hover-class="weui-cell_active">
          <div class="weui-cell__bd">
            <div>实时搜索文本3</div>
          </div>
        </navigator>
        <navigator url="" class="weui-cell" hover-class="weui-cell_active">
          <div class="weui-cell__bd">
            <div>实时搜索文本4</div>
          </div>
        </navigator>
      </div>
    </div>




     <div class="page">
      <div class="page__bd page__bd_spacing">
        <swiper :indicator-dots="indicatorDots" :autoplay="autoplay" :interval="interval" :duration="duration" :circular="circular" @change="swiperChange" @animationfinish="animationfinish">
          <div v-for="(item, index) in imgUrls" :key="index">
            <swiper-item>
              <image :src="item" class="slide-image"   />
            </swiper-item>
          </div>
        </swiper>
      </div>
    </div>
  <!-- 分类面板1 -->
  <div class="post-panel">
    <div class="post-panel__hd">
      <h3 class="post-panel__title">寻找丢失宝贝</h3>
      <a href="javascript:;" class="btn-more">更多 &gt;</a>
    </div>
    <div class="post-panel__bd">
      <ul class="post-list">
        <li class="post-item" v-for="(item, index) in items" :key="index" @click="clickHandle(item.UUID, $event)">
          <div class="post-item__pic">
            <image :src=item.PicUrl />
          </div>
          <div class="post-item__detail">
            <p class="post-item__title">{{item.Title}}</p>
            <p class="post-item__desc">{{item.desc}}</p>
          </div>
        </li>
      </ul>  
    </div>
    
  </div>
  <!-- 分类面板1 end -->

  <!-- 分类面板2 -->
  <div class="post-panel">
    <div class="post-panel__hd">
      <h3 class="post-panel__title">寻找送养宝贝</h3>
      <a href="javascript:;" class="btn-more">更多 &gt;</a>
    </div>
    <div class="post-panel__bd">
      <ul class="post-list">
      <li class="post-item" v-for="(item, index) in items" :key="index" @click="clickHandle( item.UUID, $event)">
        <div class="post-item__pic">
          <image :src="src" />>
        </div>
        <div class="post-item__detail">
          <p class="post-item__title">{{item.Title}}</p>
          <p class="post-item__desc">{{item.desc}}</p>
        </div>
      </li>
    </ul>
    </div>
  </div>
  <!-- 分类面板2 end -->

  <!-- 分类面板3 -->
  <div class="post-panel">
    <div class="post-panel__hd">
      <h3 class="post-panel__title">被拐宝贝寻家</h3>
      <a href="javascript:;" class="btn-more">更多 &gt;</a>
    </div>
    <div class="post-panel__bd">
      <ul class="post-list">
        <li class="post-item" v-for="(item, index) in items" :key="index" @click="clickHandle(item.UUID, $event)">
          <div class="post-item__pic">
            <image :src="src" />
          </div>
          <div class="post-item__detail">
            <p class="post-item__title">{{item.Title}}</p>
            <p class="post-item__desc">{{item.desc}}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
  <!-- 分类面板3 end -->

  <!-- 分类面板4 -->
  <div class="post-panel">
    <div class="post-panel__hd">
      <h3 class="post-panel__title">送养宝贝寻家</h3>
      <a href="javascript:;" class="btn-more">更多 &gt;</a>
    </div>
    <div class="post-panel__bd">
      <ul class="post-list">
        <li class="post-item" v-for="(item, index) in items" :key="index" @click="clickHandle(item.UUID, $event)">
          <div class="post-item__pic">
            <image :src="src" />
          </div>
          <div class="post-item__detail">
            <p class="post-item__title">{{item.Title}}</p>
            <p class="post-item__desc">{{item.desc}}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
  <!-- 分类面板4 end -->

  <!-- 右下角加号新建 -->
  <img src="/images/addition_fill.png" class="btn-post" @click="onPostClick"/>


  </div>
</template>

<script>
import card from '@/components/card'
export default {
  components: {
    card
  },
  data () {
    return {
      inputShowed: false,
      inputVal: '',
      indicatorDots: true,
      autoplay: true,
      interval: 5000,
      duration: 900,
      circular: true,

      imgUrls: [
        '/images/h2.png',
        '/images/head.jpg',
        '/images/h3.jpg'
      ],

      src: '',
      items: [{
        desc: '',
        PicUrl: ''
      }, {
        desc: '',
        PicUrl: ''
      }]
    }
  },
  created: function () {
    this.getSummary()
    wx.getStorage({
      key: 'nickname',
      success: function (res) {
        console.log('get storage===', res.data)
      }
    })
  },
  mounted () {
  },
  onPullDownRefresh: function () {
    wx.showLoading({
      title: '加载中'
    })
    this.getSummary()

    setTimeout(function () {
      wx.hideLoading()
    }, 2000)
    wx.stopPullDownRefresh()
  },
  methods: {
    swiperChange (e) {
      // console.log('第' + e.mp.detail.current + '张轮播图发生了滑动')
    },
    animationfinish (e) {
      // console.log('第' + e.mp.detail.current + '张轮播图滑动结束')
    },
    showInput () {
      this.inputShowed = true
    },
    hideInput () {
      this.inputVal = ''
      this.inputShowed = false
    },
    clearInput () {
      this.inputVal = ''
    },
    inputTyping (e) {
      console.log(e)
      this.inputVal = e.mp.detail.value
      console.log('输入信息为：' + e.mp.detail.value)
    },
    clickHandle (uuid, ev) {
      // console.log('clickHandle:', uuid, ev)
      wx.navigateTo({
        url: '../article/main?uuid=' + uuid,
        success: function () {
          // console.log('clickHandle success:', uuid, ev)
        },
        fail: function () {
          console.log('clickHandle fail:', uuid, ev)
        },
        complete: function () {
          // console.log('clickHandle:', uuid, ev)
        }
      })
    },
    getSummary () {
      let vm = this
      wx.request({
        url: 'https://www.srehub.com/lastest/wechat/small/summary',
        method: 'GET',
        data: {
          phone_model: 'b'
        },
        header: {
          'content-type': 'application/json'
        },
        success: function (res) {
          console.log('getSummary======', res.data)
          if (res.statusCode === 200) {
            vm.items = res.data
          } else {
            wx.showLoading({
              title: '加载失败 : ' + res.statusCode
            })
            setTimeout(function () {
              wx.hideLoading()
            }, 2000)
          }
        },
        fail: function (res) {
          wx.showLoading({
            title: '网络不可达 : ' + res.statusCode
          })
          setTimeout(function () {
            wx.hideLoading()
          }, 2000)
        }
      })
    },
    openSuccess () {
      wx.navigateTo({
        url: '../uploader/main'
      })
    },
    onPostClick () {
      wx.navigateTo({
        url: '/pages/publish/main'
      })
    }
  }
}
</script>

<style scoped>
.searchbar-result {
  margin-top: 0;
  font-size: 14px;
}
.searchbar-result:before {
  display: none;
}
.weui-cell {
  padding: 12px 15px 12px 35px;
}

.slide-image {
  width: 100%;
  height: 100%;
}

.btn-post {
    float: right;
    position: fixed;
    bottom: 40rpx;
    right: 20rpx;  
    height: 120rpx;
    width: 120rpx;
}


.bh-index-page {
  padding-bottom: 20rpx;
}

.bh-search-mod .weui-icon-search {
  vertical-align: -6rpx;
}


</style>



